// shengqu
// import CommentBar from '@/components/comment-bar.vue'; //点评条
<template>
  <div class="CommentBar">
    <div class="reviewListShow">
      <h3 class="reviewList_title">{{vTitle}}</h3>
      <div class="reviewList" v-for="(item,index) in vData" :key="item.id">
        <div class="reviewList_center">
          <div class="reviewList_portrait">
            <img :src="item.user_profile.profile.avatar_path" class="reviewList_portrait_img" @click="goUser(item.user_profile.id)"/>
          </div>
          <div class="reviewList_msg">
            <p>
              <span class="reviewList_name" @click="goUser(item.user_profile.id)">{{item.user_profile.username}}</span>
              <span class="reviewList_time">{{item.created_at}}</span>
            </p>
            <StarCard class="reviewList_starUl" :imgWidth="8" :vData="item.score" />
          </div>
          <div class="reviewList_love">
            <div class="reviewList_loveImg">
              <img src="@/assets/comment-bar/in_like_border_icon@3x.png" alt="" v-show="!item.like_state" @click="commentLove(item,item.like_state)" />
              <img src="@/assets/comment-bar/in_like_icon@3x.png" alt="" v-show="item.like_state=!!item.like_state" @click="commentLove(item,item.like_state)" />
            </div>
            <span class="reviewList_loveCount">{{item.likes}}</span>
          </div>
        </div>
        <div class="reviewList_content">
          {{item.content}}
        </div>
        <ul class="reviewList_imgShow" v-if="item.imgs_url&&item.imgs_url.length >= 1">
          <li v-for="(imgUrl,i) in item.imgs_url" :key="imgUrl+i">
            <a href="javascript:;">
              <img :src="imgUrl" @click="imgClick(item,i)"/>
            </a>
          </li>
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
import { nativeGo,} from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'

import StarCard from '@/components/star-card.vue'; //星级评分
export default {
  name: 'CommentBar',
	components: { StarCard },
  props: ['vTitle','vData'],
  data () {
    return {
    }
  },
  methods:{
		commentLove(item,_isCancel){
			item.like_state = !item.like_state
      this.$emit('commentLikeIndex',{id:item.id,isCancel:_isCancel})
		},
    imgClick(item,_i){
      this.$emit('commentImgIndex',{item,i:_i})
    },
    goUser(_id){
      nativeGo('user',{id:_id})
      if(!isApp){}
    }
  },
  mounted(){
  }

}
</script>
<style scoped lang="less">
  @m:750/375;
  .reviewList_title{
  	  margin-top: 30px*@m;
  	  font-family: PingFangSC-Medium;
    font-size: 14px*@m;
    color: #0F2540;
    letter-spacing: 0;
    line-height: 14px*@m;
  }
  .reviewList_center{
  	  width: 100%;height: 40px*@m;
  	  margin-top: 20px*@m;
  }
  .reviewList_portrait{
  	    float: left;width: 40px*@m;height: 40px*@m;
  	    border-radius: 50%;
  	    background: darkgrey;
  	    overflow: hidden;
  }
  .reviewList_portrait_img{
  	    width: 100%;height: 100%;
  	    object-fit:cover;
  }
  .reviewList_msg{
  	   float: left;
  	   margin-left: 10px*@m;
  	   padding-top: 9px*@m;
  }
  .reviewList_name{
  	    font-family: PingFangHK-Medium;
      font-size: 13px*@m;
      color: #B9B9B9;
			letter-spacing: 0;
			line-height: 13px*@m;
  }
  .reviewList_time{
  	    font-family: PingFangHK-Regular;
			font-size: 12px*@m;
			color: #D7D7D7;
			letter-spacing: 0;
			line-height: 12px*@m;
			margin-left: 6px*@m;
  }
  .reviewList_starUl{
  	   margin-top: 5px*@m;
  }
  // .reviewList_starUl li{
  // 	  float: left;
  // 	  width: 8px*@m;
  // 	  height: 8px*@m;
  // 	  margin-right: 4px*@m;
  // }
  .reviewList_love{
  	   float: right;
  	   padding-top: 9px*@m;
  }
  .reviewList_loveImg{
  	  float: left;
  	  width: 18px*@m;
  	  height: 16px*@m;
  	  overflow: hidden;
  }
  .reviewList_loveImg img{
  	   width:100%;
  	   height: 100%;
  	   object-fit:cover;
  }
  .reviewList_loveCount{
  	    opacity: 0.5;
			font-family: PingFangSC-Regular;
			font-size: 12px*@m;
			color: #BCBCBC;
			letter-spacing: 0;
			line-height: 12px*@m;
			float: left;
			margin-top:2px*@m;
			margin-left: 8px*@m;
  }
  .reviewList_content{
  	   width: 100%;
  	   margin-top: 12px*@m;
  	   font-family: PingFangSC-Regular;
			font-size: 16px*@m;
			color: #2D3F56;
			letter-spacing: 0;
			line-height: 24px*@m;
  }
  .reviewList_imgShow{
  	   width: 100%;
  	   margin-top: 10px*@m;
  	   overflow: hidden;
  }
  .reviewList_imgShow li{
  	   float: left;
  	   width: 105px*@m;
  	   height: 70.3px*@m;
  	   overflow: hidden;
  	   border-radius: 8px*@m;
  }
  .reviewList_imgShow li:nth-child(2){
  	     margin-right:10px*@m;
  	     margin-left:10px*@m;
  }
  .reviewList_imgShow a{
  	  display: block;
  	  height: 70.3px*@m;
  }
  .reviewList_imgShow img{
  	   width: 100%;
  	   height: 100%;
  	   object-fit:cover;
  }

</style>


