// shengqu
// import StarCard from '@/components/star-card.vue'; //星级评分
<template>
	 <div v-if="true">
			<ul  class="ProductBar_star">
				<li v-for="(v,i) in 5" :key="i">
					<img v-if="i+1<=vData" src="@/assets/comment-bar/in_star_light_icon@3x.png" :style="{width:imgWidth?imgWidth*2+'px':false,height:imgWidth?imgWidth*2+'px':false}"/>
					<img v-else src="@/assets/comment-bar/in_star_null_icon@3x.png" :style="{width:imgWidth?imgWidth*2+'px':false,height:imgWidth?imgWidth*2+'px':false}" />
				</li>
			</ul>
			<span class="ProductBar_score" v-show="!noScore">{{vData}}</span>
	</div>
	
</template>

<script>
	export default {
		props:['vData','imgWidth','noScore'],//noScore不显示分数的情况
		data(){
			return{
				
			}
		},
	  mounted(){
    }
	}
</script>

<style scoped lang="less">
  @m:750/375;
  
  .ProductBar_star{
  	   float: left;
  	   
  }
  .ProductBar_star li{
  	   float: left;
  	   margin-right: 4px*@m;
  }
  .ProductBar_star li img{
  	   width: 12px*@m;
  	   height: 12px*@m;
  }
  .ProductBar_score{
  	    font-family: PingFangSC-Regular;
			font-size: 13px*@m;
			color: #BBBDBF;
			letter-spacing: 0;
			text-align: center;
			line-height: 13px*@m;
			float: left;
  }
  
  
  
  
  
  
</style>


